	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"JavaScript is a language with more than its share of bad parts. It went from non-existence to global adoption in an alarmingly short period of time. It never had an interval in the lab when it could be tried out and polished. JavaScript has some extraordinarily good parts. In JavaScript there is a beautiful, highly expressive language that is buried under a steaming pile of good intentions and blunders. The best nature of JavaScript was so effectively hidden that for many years the prevailing opinion of JavaScript was that it was an unsightly, incompetent abomination. This session will expose the goodness in JavaScript, an outstanding dynamic programming language. Within the language is an elegant subset that is vastly superior to the language as a whole, being more reliable, readable and maintainable.","meta":{"duration":"PT1H3M48S"},"presenter":{"name":"Doug Crockford"},"source":{"id":"hQVTIJBZook","provider":"youtube"},"slug":"javascript-the-good-parts","tag":["JavaScript"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/hQVTIJBZook/default.jpg","height":90,"width":120}},"title":"JavaScript: The Good Parts"}}]}},"pathContext":{"speaker":"Doug Crockford"}}

